import { Injectable } from '@angular/core';
import { Angular2TokenService } from '../angular2-token.service';
import { Http, Headers, RequestOptions, Response } from '@angular/http';

import { SERVER_URL } from '../common';
import 'rxjs/Rx';
@Injectable()
export class ExploreService {
    constructor(private http: Http, private authService: Angular2TokenService) { }
    
    getFeed(page_no){
        return this.http.post(SERVER_URL + "/users/feed/suggested",  "page="+page_no ,this.getHeader()).map((response: Response) => response.json());
    }

    getHeader() {
        let baseHeaders: { [key:string]: string; } = {
          'Content-Type': 'application/x-www-form-urlencoded; charset=UTF-8',
          'Accept': 'application/json',
          'X-Requested-With': 'XMLHttpRequest'
        };

        let baseRequestOptions = new RequestOptions({
            headers: new Headers(baseHeaders),
            withCredentials: true,
        });

        return baseRequestOptions;
    }
}


