import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { FormsModule } from '@angular/forms';
import { HttpModule } from '@angular/http';
import {TypeaheadModule} from 'ngx-bootstrap/typeahead';
import { LaddaModule } from 'angular2-ladda';

//import the route
import { AtmosphereRoutes } from './app.routes';

import { AppComponent } from './app.component';
import { LoginComponent } from './login/login.component';
import { SidebarComponent } from './sidebar/sidebar.component';
import { NotificationComponent } from './sidebar/notification/notification.component';
import { ExploreComponent } from './sidebar/explore/explore.component';

//for services
import { NotificationService, ExploreService,InteractService } from './services';

import { GeneralService } from './general.service';
import { Angular2TokenService } from './angular2-token.service';
import { AtmosphereService } from './atmosphere.service';
import { MixerComponent } from './custom/mixer/mixer.component';


@NgModule({
  declarations: [
    AppComponent,
    LoginComponent,
    SidebarComponent,
    NotificationComponent,
    ExploreComponent,
    MixerComponent,
  ],
  imports: [
    BrowserModule,
    FormsModule,
    HttpModule,
    AtmosphereRoutes,
    TypeaheadModule.forRoot(),
    LaddaModule
  ],
  providers: [
    NotificationService,
    ExploreService, 
    Angular2TokenService,
    GeneralService,
    InteractService,
    AtmosphereService,
    ExploreComponent
  ],
  bootstrap: [AppComponent]
})
export class AppModule { }



