import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { User } from '../models';
import { Angular2TokenService, USER_SIGNED_INFO } from '../angular2-token.service';

@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.css']
})
export class LoginComponent implements OnInit {

  public errorMessage: string = "";
  public is_signingin: boolean = false;
  public model: any = [];

  constructor(
      private router: Router,
      public authService: Angular2TokenService,
  	) { }

  ngOnInit() {
  }

  signin(loginForm){
    if(!loginForm.form.valid)
    {
      this.errorMessage = "Please enter your email and password correctly.";
      return;
    }
    // this.appState.setLoading("Signing In...");
    this.is_signingin = true;
    // console.log(loginForm.form.serialize());
    this.authService.signIn(this.model.email, this.model.password)
     .subscribe(result => {
      this.errorMessage = "";
      this.is_signingin = false;
      this.router.navigate(['']);
     },error => {
       this.errorMessage = error.json().errors;
       this.is_signingin = false;
     });
  }
  

}



